import {
    createLocalAudioTrack,
    createLocalVideoTrack,
    LocalAudioTrack,
    LocalVideoTrack
} from 'livekit-client';

/**
 * 安全创建本地轨道的工具函数
 * 支持条件创建和错误容错，避免单个轨道失败导致整体失败
 */
export interface CreateLocalTracksOptions {
    initialAudioEnabled: boolean;
    initialVideoEnabled: boolean;
    audioConfig?: {
        echoCancellation?: boolean;
        noiseSuppression?: boolean;
        autoGainControl?: boolean;
    };
    videoConfig?: {
        width?: number;
        height?: number;
        frameRate?: number;
    };
}

export interface CreateLocalTracksResult {
    audioTrack: LocalAudioTrack | null;
    videoTrack: LocalVideoTrack | null;
}

export const createLocalTracksSafely = async (
    options: CreateLocalTracksOptions
): Promise<CreateLocalTracksResult> => {
    const {
        initialAudioEnabled,
        initialVideoEnabled,
        audioConfig = {
            echoCancellation: true,
            noiseSuppression: true,
            autoGainControl: true,
        },
        videoConfig = {
            width: 1280,
            height: 720,
            frameRate: 24,
        }
    } = options;

    console.log('开始安全创建本地轨道 - audio:', initialAudioEnabled, 'video:', initialVideoEnabled);

    try {
        const results = await Promise.allSettled([
            // 音频轨道创建
            initialAudioEnabled 
                ? createLocalAudioTrack(audioConfig)
                : Promise.resolve(null),
            // 视频轨道创建  
            initialVideoEnabled 
                ? createLocalVideoTrack({ resolution: videoConfig })
                : Promise.resolve(null),
        ]);
        
        const audioTrack = results[0].status === 'fulfilled' ? results[0].value : null;
        const videoTrack = results[1].status === 'fulfilled' ? results[1].value : null;
        
        // 记录轨道创建结果
        if (initialAudioEnabled) {
            if (results[0].status === 'rejected') {
                console.error('音频轨道创建失败:', results[0].reason);
                console.warn('音频设备不可用，会议将以静音模式继续');
            } else {
                console.log('音频轨道创建成功');
            }
        }
        
        if (initialVideoEnabled) {
            if (results[1].status === 'rejected') {
                console.error('视频轨道创建失败:', results[1].reason);
                console.warn('视频设备不可用，会议将以音频模式继续');
            } else {
                console.log('视频轨道创建成功');
            }
        }
        
        // 至少要有一个轨道成功，否则抛出错误
        if (!audioTrack && !videoTrack && (initialAudioEnabled || initialVideoEnabled)) {
            throw new Error('无法创建任何媒体轨道，请检查设备权限和可用性');
        }
        
        return { audioTrack, videoTrack };
        
    } catch (error) {
        console.error('创建本地轨道完全失败:', error);
        throw error;
    }
};

/**
 * 安全发布轨道到房间
 */
export const publishTracksSafely = async (
    room: any,
    audioTrack: LocalAudioTrack | null,
    videoTrack: LocalVideoTrack | null
) => {
    const publishPromises = [];
    
    if (audioTrack) {
        publishPromises.push(room.localParticipant.publishTrack(audioTrack));
        console.log('发布音频轨道');
    }
    
    if (videoTrack) {
        publishPromises.push(room.localParticipant.publishTrack(videoTrack));
        console.log('发布视频轨道');
    }
    
    if (publishPromises.length > 0) {
        await Promise.all(publishPromises);
        console.log('轨道发布完成');
    } else {
        console.warn('没有轨道需要发布');
    }
};

/**
 * 安全设置轨道初始状态
 */
export const setInitialTrackStates = async (
    audioTrack: LocalAudioTrack | null,
    videoTrack: LocalVideoTrack | null,
    initialAudioEnabled: boolean,
    initialVideoEnabled: boolean
) => {
    if (audioTrack && !initialAudioEnabled) {
        await audioTrack.mute();
        console.log('音频轨道已静音');
    }
    
    if (videoTrack && !initialVideoEnabled) {
        await videoTrack.mute();
        console.log('视频轨道已关闭');
    }
};